import { CustomCodeBlockRendererPlugin } from "../CustomCodeBlockRendererPlugin";
import HighlightCodeBlock from "./HighlightCodeBlock.vue"
import { languages } from "@codemirror/language-data";
import type { Extension } from "@codemirror/state"
import type { CorePlugin } from "../CorePlugin";

export interface HighlightPluginConfig {
  theme?: Extension
}

class HighlightPlugin extends CustomCodeBlockRendererPlugin implements CorePlugin {
  cmMarkdownConfig: object;

  constructor(config?: HighlightPluginConfig) {
    super({
      default: [HighlightCodeBlock, config]
    })
    this.cmMarkdownConfig = {
      languages
    }
  }
}

export default function highlight(config?: HighlightPluginConfig) {
  return new HighlightPlugin(config)
}